<template>
    <div >
      <table class='table table-striped table-hover'>
          <thead>
              <tr>
                <th>#</th>
                <th>Ver</th>
                <th>Cedula</th>
                <th>Nombre</th>
                <th>Apellidos</th>
                <th>Celular</th>
                <th>Direccion</th>
                <th>Cobro</th>
                <th>Editar</th>
                <th v-if="getPerfil==='admin'">Eliminar</th>
              </tr>     
          </thead>
          <tbody>
              <tr  v-for="soc, index in getClientesAll" :key="soc._id">
                    <td>{{index + 1}}</td>
                    <td>
                        <router-link 
                            class="btn btn-info"
                            :to="{name:'rifa-panel', params:{id:soc._id}}"
                        >
                            VER
                        </router-link>
                    </td>
                  <td>{{soc.cedula}}</td>
                  <td>{{soc.nombre}}</td>
                  <td>{{soc.apellidos}}</td>
                  <td>{{soc.celular}}</td>
                  <td>{{soc.direccion}}</td>
                  <td>{{soc.diacobro}}</td>
  
                  <td>
                      <button
                          class='btn btn-secondary'
                          @click="setEdit(soc)"
                          :disabled="getIsLoading"
                      >
                          Editar
                      </button>
                  </td>
  
                  <td>
                      <button 
                          class='btn btn-danger'
                          :disabled="getIsLoading"
                          @click="deleteCliente(soc._id)"
                          v-if="getPerfil==='admin'"
                      >
                          Eliminar
                      </button>
                  </td>
              </tr>
                      
          </tbody>
      
      </table>
    </div>
  </template>
  
  <script stup>

  import { mapActions, mapGetters, mapMutations } from 'vuex'

      export default {
        
          computed:{
              
              ...mapGetters('cliente',['getClientesAll','getIsLoading']),
              ...mapGetters('auth',['getPerfil'])
          },
          methods:{
              ...mapActions('cliente', ['getClientes', 'deleteCliente']),
              ...mapMutations('cliente',['setEdit']),
              ver(){
                this.$router.push('rifa-panel')
              }
          },

          created(){
             this.getClientes()
          }
  
      }
      
  </script>
  
  <style>
  
  </style>